*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family:sans-serif;
  background: #9c27b0;
  color: #fff;
}
.App{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.App h1{
  text-align: start;
  background: #9c27b0;
  padding: 10px;
  font-size: 1.5rem;
}

.box{
  width: 80%;
  min-height: 400px;
  text-align: center;
  padding: 25px;
  background: #fff;
}
.addCourses{
  margin-top: 2rem;
}
.addCourses form{
  display: flex;
}
.addCourses form #nameCourse{
  flex: 6;
  height: 50px;
  outline: 0;
  border: 0;
  border-bottom: 1px solid #9e9e9e;
}
.addCourses form #Add{
  flex:1;
  color: #fff;
  background: #2196f3;
  outline: 0;
  border: 0;
  text-transform: capitalize;
  font-size: 15px;
  cursor: pointer;
}
.showData{
  margin-top: 3rem;
}
.showData .row
{
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  align-items: center;

}
.showData .row p
{
  color: #9c27b0;
}
.showData .row #delete
{
  border: 0;
  outline: 0;
  background: #f44336;
  color: #fff;
  width: 100px;
  font-size: 15px;
  font-weight: 700;
  cursor: pointer;
  height: 40px;
}